import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { FloatingLabelComposer } from '@Green-Dot-Corporation/eureka-ui-form-elements';
import TextareaAutosize from './TextareaAutosize';

// Styles
import './styles/message-input.scss';

class MessageTextarea extends PureComponent {
    static propTypes = {
        handleResize: PropTypes.func,
        handleMessageChange: PropTypes.func,
        handleKeyDown: PropTypes.func,
        handleKeyPress: PropTypes.func,
        msgMaxLength: PropTypes.number,
        lineHeight: PropTypes.number,
        maxRows: PropTypes.number,
    };

    static defaultProps = {
        msgMaxLength: 280,
        lineHeight: 27,
        maxRows: 3,
    };

    render() {
        const { msgMaxLength, lineHeight, maxRows, ...rest } = this.props;

        return (
            <TextareaAutosize
                className={cx(`${this.baseCls}__textarea--autosize`)}
                maxRows={maxRows}
                lineHeight={lineHeight}
                maxLength={msgMaxLength}
                onResize={this.handleResize}
                onChange={this.handleMessageChange}
                onKeyDown={this.handleKeyDown}
                onKeyPress={this.handleKeyPress}
                {...rest}
            />
        );
    }

    handleResize = (e) => {
        const { handleResize } = this.props;
        handleResize && handleResize(e);
    };

    handleMessageChange = (e) => {
        const { handleMessageChange } = this.props;
        handleMessageChange && handleMessageChange(e);
    };

    handleKeyDown = (e) => {
        const { handleKeyDown } = this.props;
        handleKeyDown && handleKeyDown(e);
    };

    handleKeyPress = (e) => {
        const { handleKeyPress } = this.props;
        handleKeyPress && handleKeyPress(e);
    };

    baseCls = 'message-input';
}

const MessageInput = FloatingLabelComposer(MessageTextarea);
export default MessageInput;
