import GatewayHttpClient from '../util/GatewayHttpClient';

export const chatUrls = {
    getConfigUrl: '/api/communication/config',
    leaveMessageUrl: '/api/communication/leaveagentmessage',
    requestUserTokenUrl: '/api/communication/user/token/request?component=CHAT',
    startConversationUrl: '/api/communication/conversation/start',
    endConversationUrl: '/api/communication/conversation/end',
};
export default {
    async getChatConfig() {
        const config = await GatewayHttpClient.get(chatUrls.getConfigUrl);
        const resp = {
            hoop: config.hoop,
            isAgentOnline: config.isagentonline,
            ewt: config.ewt,
            isLeaveAgentMessageEnabled: config.isleaveagentmessageenabled,
            leaveAgentMessage: config.leaveagentmessage,
            userInactiveInterval: config.userinactiveinterval,
            isSuccess: config.issuccess,
            errors: config.errors,
        };

        return resp;
    },
    async leaveMessage(leaveMessageRequest) {
        const rawResp = await GatewayHttpClient.post(
            chatUrls.leaveMessageUrl,
            leaveMessageRequest,
        );
        const resp = {
            isSuccess: rawResp.issuccess,
            errors: rawResp.errors,
        };

        return resp;
    },
    async requestUserToken() {
        const rawResp = await GatewayHttpClient.post(
            chatUrls.requestUserTokenUrl,
        );
        const resp = {
            isSuccess: rawResp.issuccess,
            errors: rawResp.errors,
            token: rawResp.token,
        };

        return resp;
    },
    async startConversation() {
        const startConversationResp = await GatewayHttpClient.post(
            chatUrls.startConversationUrl,
        );
        const resp = {
            isSuccess: startConversationResp.issuccess,
            errors: startConversationResp.errors,
            conversationSid: startConversationResp.conversationSid,
        };

        return resp;
    },
    async endConversation(conversationSid, taskSid) {
        const endConversationResp = await GatewayHttpClient.post(
            chatUrls.startConversationUrl,
            {
                conversationSid,
                taskSid,
            },
        );
        const resp = {
            isSuccess: endConversationResp.issuccess,
            errors: endConversationResp.errors,
            conversationSid: endConversationResp.conversationSid,
        };

        return resp;
    },
};
