import ChatService from './ChatService';
import TwilioClient from './clients/TwilioClient';

export default class TwilioService {
    static getClient = async () => {
        if (!TwilioClient.client) {
            const token = await this.fetchToken();
            await TwilioClient.createClient(token);
        }
        await this.checkClientConnectionState();

        return TwilioClient.client;
    };

    static getConversation = async () => {
        if (!TwilioClient.conversation) {
            await this.getClient();
            const sid = await this.fetchConversationSid();
            await TwilioClient.getConversationBySid(sid);
        }

        return TwilioClient.conversation;
    };

    static fetchToken = async () => {
        const { token } = await ChatService.requestUserToken();

        return token;
    };

    static fetchConversationSid = async () => {
        const { conversationSid } = await ChatService.startConversation();

        return conversationSid;
    };

    static checkClientConnectionState = async () => {
        switch (TwilioClient.client.connectionState) {
            // client connection is denied because of invalid JWT access token. User must refresh token in order to proceed
            case 'denied': {
                const refreshToken = await this.fetchToken();
                await TwilioClient.refreshToken(refreshToken);
                break;
            }

            // client connection is in a permanent erroneous state. Client re-initialization is required.
            case 'error': {
                const token = await this.fetchToken();
                await TwilioClient.createClient(token);
                break;
            }

            default:
                break;
        }
    };

    static destoryClient = async () => {
        const { client } = TwilioClient;

        if (client) {
            await client.shutdown();
        }
    };

    static sendMessage = async (message) => {
        if (!TwilioClient.conversation) {
            await this.getClient();
            const sid = await this.fetchConversationSid();
            await TwilioClient.getConversationBySid(sid);
        }

        return TwilioClient.sendMessage(message);
    };
}
